<template>
  <div>
    <div class="nav" style="background: rgba(255, 255, 255, 0.9)">
      <div>
        国家:<br />
        <select name="" id="" v-model="country" @change="checkEmial">
          <option
            v-for="(item, index) in countryList"
            :key="index"
            :value="item"
          >
            {{ item }}
          </option>
        </select>
      </div>
      <div>
        工厂:<br />
        <select name="" id="" v-model="factory" @change="checkEmial">
          <option
            v-for="(item, index) in factoryList"
            :key="index"
            :value="item.factory"
          >
            {{ item.factory }}
          </option>
        </select>
      </div>
      <div>
        起始时间:
        <input
          type="date"
          name=""
          id=""
          v-model="beginTime"
          @change="checkEmial"
        />
      </div>
      <div>
        结束时间：
        <input type="date" v-model="endTime" @change="checkEmial" />
      </div>
    </div>
    <div class="main" style="margin-left: 13vw">
      <table class="emailFeedback">
        <thead>
          <tr>
            <th>序号</th>
            <th>品名</th>
            <th>SKU</th>
            <th>国家</th>
            <th>订单号</th>
            <th>货柜号</th>
            <th>工厂</th>
            <th>问题类型</th>
            <th>缺件\破损情况</th>
            <th>附件预览</th>
            <th>提交时间</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in tableList" :key="index">
            <td>{{ index + 1 + (page - 1) * 50 }}</td>
            <td>{{ item.commodity_name }}</td>
            <td>{{ item.sku }}</td>
            <td>{{ item.country }}</td>
            <td>{{ item.order_number }}</td>
            <td>{{ item.container_num }}</td>
            <td>{{ item.factory }}</td>
            <td>{{ item.problem_type }}</td>
            <td>
              <div class="tdDiv">{{ item.problem_content }}</div>
            </td>
            <td>
              <a :href="item.attachment_address" download>{{
                getfilename(item.attachment_address)
              }}</a>
            </td>
            <td>{{ item.upload_time }}</td>
          </tr>
        </tbody>
      </table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pagetotle"
        style="margin: 1vh 0 0 38vw"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      beginTime: "", //开始时间
      endTime: "", //结束时间
      country: "", //国家
      factory: "", //工厂
      tableList: [], //表格数据
      total: 0, //页码当前页
      page: 1, //
      countryList: [], //国家列表
      factoryList: [] //工厂列表
    };
  },
  methods: {
    checkEmial() {
      //左侧下拉框筛选表格数据
      axios({
        url: this._url2 + "server/customer_management/factory_feedback/",
        method: "get",
        params: {
          country: this.country,
          page: 1,
          begin_time: this.beginTime,
          over_time: this.endTime,
          factory: this.factory
        }
      }).then(res => {
        if (res.data.code == 200) {
          console.log(res);
          this.tableList = res.data.data;
          this.$nextTick(() => {
            $(".emailFeedback>tbody").scrollTop(0);
          });
          this.total = res.data.count[0].count / 5;
          this.page = 1;
        }
      });
    },
    getfilename(x) {
      //获取文件名函数
      let pos = x.lastIndexOf("/");
      return x.substring(pos + 1);
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      this.page = newPage;
      axios({
        url: this._url2 + "server/customer_management/factory_feedback/",
        method: "get",
        params: {
          country: this.country,
          page: this.page,
          begin_time: this.beginTime,
          over_time: this.endTime,
          factory: this.factory
        }
      }).then(res => {
        if (res.data.code == 200) {
          console.log(res);
          // emailFeedback
          this.tableList = res.data.data;
          this.$nextTick(() => {
            $(".emailFeedback>tbody").scrollTop(0);
          });
          this.total = res.data.count[0].count / 5;
        }
      });
    }
  },
  created() {
    //页面刚进入获取表格数据
    axios({
      url: this._url2 + "server/customer_management/factory_feedback/",
      method: "get",
      params: {
        page: 1
      }
    }).then(res => {
      if (res.data.code == 200) {
        console.log(res);
        this.tableList = res.data.data;
        this.total = res.data.count[0].count / 5;
        // this.countryList = res.data.country_data;
        this.factoryList = res.data.factory_data;
      }
    });
    axios({
      //获取国家下拉框（权限用）
      url: this._url2 + "server/personnel_management/get_sign_country/",
      method: "get"
    }).then(res => {
      this.countryList = res.data.data;
    });
  }
};
</script>

<style scoped>
table {
  /*width: 84vw;*/
  margin: 10px 0px;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 80vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 6vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 40px;
  line-height: 40px;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 40px;
  line-height: 40px;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}

.nav {
  position: fixed;
  top: 8vh;
  left: 0;
  width: 12vw;
  height: 92vh;
  border-right: 2px #ededed solid;
  font-size: 0.8rem;
}

.nav div {
  margin: 2vh;
}

.nav select,
.nav input {
  width: 8vw;
  height: 3vh;
}

table tr th:nth-child(1),
table tr td:nth-child(1),
table tr th:nth-child(4),
table tr td:nth-child(4),
table tr th:nth-child(6),
table tr td:nth-child(6),
table tr th:nth-child(7),
table tr td:nth-child(7) {
  width: 3vw;
}

table tr th:nth-child(2),
table tr td:nth-child(2),
table tr th:nth-child(3),
table tr td:nth-child(3) {
  width: 7.9vw;
}

table tr th:nth-child(5),
table tr td:nth-child(5),
table tr th:nth-child(10),
table tr td:nth-child(10) {
  width: 9vw;
}

table tr th:nth-child(8),
table tr td:nth-child(8) {
  width: 6vw;
}

table tr th:nth-child(11),
table tr td:nth-child(11) {
  width: 7vw;
}

table tr th:nth-child(9),
table tr td:nth-child(9) {
  width: 27vw;
}

.tdDiv {
  max-height: 10vh;
  overflow: hidden;
  text-overflow: ellipsis;
  overflow-y: scroll;
  line-height: 20px;
}
</style>



